import { useState } from 'react'

export default function Selection({ position, index, sound }) {
  const [checked, setChecked] = useState(false)

  if (position === index + 1 && checked) {
    sound.sound.play()
  }

  const handleCheckboxChange = () => {
    setChecked(!checked)
  }

  const handleCheckboxClick = () => {
    if (!checked) sound.sound.play()
  }

  return (
    <td key={sound.sound.toString()} className={`selection ${checked ? 'highlight' : ''}`}>
      <input type="checkbox" onChange={handleCheckboxChange} {...{ checked }} onClick={handleCheckboxClick} />
    </td>
  )
}
